import { BLOG_ITEM_MARK, BLOG_ITEM_UNMARK } from '../actionTypes/blogActionType';
import AppDispatcher from '../appDispatcher';

class BlogAction{
    murkAsRead(itemId){
        appDispatcher.dispatch({
            type: actionTypes.BLOG_ITEM_MARK,
            itemId
        });
    }
    murkAsUnread(itemId){
        appDispatcher.dispatch({
            type: actionTypes.BLOG_ITEM_UNMARK,
            itemId
        });
    }
}

export default BlogAction;



