import EventEmitter from 'events';
import appDispatcher from '../appDispatcher';
import { BLOG_ITEM_MARK, BLOG_ITEM_UNMARK } from '../actionTypes/blogActionType';

const blogItems = [{
        id: 1,
        itemHeader: 'Blog item 1',
        itemDescription: 'test description of item Lorem ipsum dollar Lorem ipsum dollar',
        marked: false
    },
    {
        id: 2,
        itemHeader: 'Blog item 2',
        itemDescription: 'test description of item 2 Lorem ipsum dollar',
        marked: false
    },{
        id: 3,
        itemHeader: 'Blog item 3',
        itemDescription: 'test description of item 3 Lorem ipsum dollar Lorem ipsum dollar',
        marked: false
}];

class BlogStore{
    constructor(props){
        this.items = props;
    }

    get actionChange(){
        return 'change';
    }

    getAll(){
        return this.items;
    }

    getById(itemId){
        return this.items.reduce((temp, item)=>{
            if(item.id == itemId) temp = item;
            return temp;
        }, {});
    }

    murkAsRead(itemId){
        this.getById(itemId).marked = true;
        this.emmit(this.actionChange);
    }

    murkAsUnread(itemId){
        this.getById(itemId).marked = false;
        this.emmit(this.actionChange);
    }

    addChangeListener(listener){
        this.addListener(this.actionChange, listener);
    }

    removeChangeListener(listener){
        this.removeListener(this.actionChange, listener);
    }
}

let blogStore = new BlogStore(blogItems);
blogStore = Object.assign(blogStore, EventEmitter.prototype);

appDispatcher.register(function(action){
    switch(action.type){
        case BLOG_ITEM_MARK:
            blogStore.murkAsRead(action.itemId);
            break;
        case BLOG_ITEM_UNMARK:
            blogStore.murkAsUnread(action.itemId);
            break;
    }
});

export default blogStore;



