import React, { PropTypes } from 'react';
import BlogItem from '../blogItem/blogItem';

const BlogList = ({ blogHeader, blogItems, onClick }) => {
    const blogListItems = blogItems.map((blogItem, indx) => {
        const props = {
            btnProps:{
                id: blogItem.id,
                onClick: function(ev){
                        ev.preventDefault();
                        console.log(blogItem.marked, blogItem.id);
                        onClick(blogItem.marked, blogItem.id);
                }
            },
            itemHeader: blogItem.itemHeader,
            itemDescription: blogItem.itemDescription,
            marked: blogItem.marked
        }
        return <BlogItem key={indx} {...props}/>;
    });

    return (
        <section>
            <header><h1>{blogHeader}</h1></header>
            {blogListItems}
        </section>
    );
}

BlogList.propTypes = {

}

export default BlogList;



