import React, { Component } from 'react';
import BlogList from '../../components/blogList/blogList';
import Header from '../../components/header/header';
import './app.css';
import blogStore from '../../stores/blogStore';
import blogActions from '../../actions/blogActions';

class App extends Component{
    constructor(props){
        super(props);
        this.state = { blog: blogStore.getAll() };
        //this.onClickMark = this.onClickMark.bind(this);
        this.onStoreChange = this.onStoreChange.bind(this);
    }
    componentDidMount(){
        blogStore.addChangeListener(this.onStoreChange);
    }
    componentWillUnmount(){
        blogStore.removeChangeListener(this.onStoreChange);
    }
    onStoreChange(){
        this.setState({  blog: blogStore.getAll() });
    }
    onClickMark(isMarked, itemId){
        if(isMarked){
            blogActions.murkAsRead(itemId);
        }else{
            blogActions.murkAsUnread(itemId);
        }
    }
    render(){
        return(<main>
            <Header />
            <BlogList blogHeader={this.props.blogHeader} blogItems={this.state.blog} onClick={this.onClickMark}/>;
        </main>);
    }
}

export default App;



