import React, { Component, PropTypes } from 'react';
import styles from './header.css';

class Header extends Component{
    constructor(props){
        super(props);
    }
    render(){
        return (<header className={styles.mainHeader}>
            <nav>
                <ul className={styles.mainMenu}>
                    <li className={styles.mainMenuItem}>Home</li>
                    <li className={styles.mainMenuItem+' '+styles.activeMenu}>Blog</li>
                    <li className={styles.mainMenuItem}>About</li>
                    <li className={styles.mainMenuItem}>Contact</li>
                    <li className={styles.mainMenuItem}>{this.props.user ? 'Logout' : 'Login'}</li>
                </ul>
            </nav>
            <figure className={styles.logo} src='../../../dist/images/logo.jpg'></figure>
        </header>);
    }
}

Header.defaultProps = {
    user: null
}

export default Header;



