import React, { PropTypes } from 'react';
import Button from '../button/button';
import styles from './blogItem.css';

const BlogItem = ({ btnProps, itemHeader, itemDescription, marked }) => {
    const styleClass = marked ? styles.marked : styles.unmarked;
    const btnText = marked ? 'unmarked' : 'marked';
    const tooltipText = `"Mark as Unread" / "Mark as Read"`;
    btnProps = Object.assign({}, { btnText }, btnProps, { tooltipText });
    
    return (
        <article>
            <h3 className={styleClass}>{itemHeader}</h3>
            <Button {...btnProps}/>
            <p>{itemDescription}</p>
        </article>
    );
}

BlogItem.propTypes = {
    btnProps: PropTypes.object.isRequired
}

export default BlogItem;



